.navbar {
    width: 100%;
    height: 50px;
    background-color: #41D6BB;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: 0.5s;
}

.navbar ul {
    display: flex;
    list-style: none;
}

.navbar a {
    text-decoration: none;
    padding: 10px;
    color: #12463D;
    font-weight: 700;
}

.navbar .logo {
    font-size: 25px;
    text-transform: uppercase;
    font-weight: 800;
}

.port {
    color: #12463D;
}

.navbar .logo .folio {
    color: #CFECE7;
}

.toggle {
    position: absolute;
    right: 10px;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    top: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    border-radius: 5px;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
}

.toggle .line {
    transition: 0.5s;
    width: 30px;
    height: 5px;
    background-color: #12463D;
}

@media (max-width:600px) {
    .navbar ul {
        margin-top: 30px;
        flex-direction: column;
        opacity: 0;
        pointer-events: none;
        gap: 15px;
        justify-content: space-evenly;
        align-items: center;
    }
    .toggle {
        opacity: 1;
        pointer-events: all;
    }
    .overlay {
        position: absolute;
        width: 100%;
        height: 50%;
        margin-top: 300px;
        opacity: 0;
        pointer-events: all;
        z-index: 100000;
        background-color: red;
        left: 0;
    }
}