.Footer{
    margin-top: 50px;
    width: 100%;
    height: 300px;
    background-color: #12463D;
    border: 2px solid #CFECE7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Footer .Contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 150px;
}
.Contact img{
    width: 100px;
}
.Footer p{
    color: #CFECE7;
}